*{
  font-family: 'Poppins', sans-serif;
  box-sizing: border-box;
  overflow-x: hidden;
}

html {
  margin: -15px;
  padding: 0;
  scroll-behavior: smooth;
  background-color: rgb(250, 239, 226);
}

nav {
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(80deg, rgba(252,70,107,1) 0%, rgba(237,165,180,1) 100%);
}

.nav--title {
  font-weight: 400;
  line-height: 5rem;
  letter-spacing: 6px;
}

.cards-list {
  display: flex;
  flex-direction: column;
  padding: 5rem 3em;
}

.card {
  display: flex;
  justify-content: center;
  flex-direction: row;
  padding: 2.5rem 0;
  overflow: hidden;
}

.card--img-box {
  overflow: hidden;
  transition: transform 0.25s;

}

.card--img {
  /* height: 100%; */
  width: 22rem;
  object-fit: contain;
  border-radius: 10px;
}

.card--img-box:hover {
  transform: scale(1.5);
}

.card--info {
  margin-left: 1rem;
  max-width: 500px;
}

.pin--img {
  width: 1.1rem;
  margin-right: 5px;
}

.card--location {
  display: contents;
  font-size: 0.75rem;
  letter-spacing: 0.17em;
  text-transform: uppercase;

}

.card--location-url {
  font-size: 0.75rem;
  color: gray;
  margin-left: 10px;
}

.card--title {
  font-size: 2rem;
  margin: 0.5rem 0;
  font-weight: 700;
}

.card--date {
  font-size: 0.9rem;
}

.bold {
  font-weight: bold;
}

.card--description {
  max-width: 45rem;
  margin-top: 15px;
  font-weight: 400;
  font-size: 0.8rem;
  line-height: 150%;
  text-align: justify;
}

